.Grid {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.gridRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.cell {
    position: relative;
    height: 25px;
    width: 25px; 
    border: 1px solid grey;
}

.live {
    background-color: black; 
}

.dead {
    background-color: white;
}


